import * as React from 'react';
import { PluginRoles } from '@ali/4ever-bamboo';;
import { IBasicButton, IToolbarButtonConfig, IconIncreaseIndent } from '@ali/4ever-component';;
import { Controller } from '@ali/4ever-cangjie';
import { INCREASE_HOT_KEY_TIP } from '../../utils/hotKeyTips';
import { increaseIndent } from '../../actions';
;

export interface IIncreaseIndentButtonProps extends IToolbarButtonConfig {
  controller: Controller;
  locale?: Record<string, string>;
}

export default function createIncreaseIndentButton(props: IIncreaseIndentButtonProps): IBasicButton {
  const { controller, locale, icon, title, tooltip, badgeDot, disabled } = props;

  const handleClick = () => controller.run('onAction', increaseIndent());

  const { isCollapsedAtEdge } = controller.value.selection;
  const isDisabled = disabled || isCollapsedAtEdge || controller.query('isSelectionInHr') || controller.query('isSelectionInListSymbol');


  const buttonConfig: IBasicButton = ({
    icon: icon || <IconIncreaseIndent />,
    onClick: handleClick,
    title: title || locale?.rightButtonTitle,
    tooltip: {
      title: title || locale?.rightButtonTitle,
      shortCut: INCREASE_HOT_KEY_TIP,
      ...tooltip,
    },
    badgeDot,
    disabled: isDisabled,
    role: PluginRoles.indentIncrease,
    testid: "bi-toolbar-incindent"
  });

  return buttonConfig;

}
